import { Component, OnInit } from '@angular/core';
import { FederacaoService } from '../../../services/federacao.service';
import {ValidateService} from '../../../services/validate.service';
import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from '@angular/router';

@Component({
  selector: 'app-federacao-add',
  templateUrl: './federacao-add.component.html',
  styleUrls: ['./federacao-add.component.css']
})
export class FederacaoAddComponent implements OnInit {
  name : String;
  razaosocial: String;
  cnpj: String;
  status: Boolean;
  email: String;

  constructor(
    private validateService: ValidateService,
    private flashMessage:FlashMessagesService,
    private federacaoService:FederacaoService,
    private router: Router) { }

  ngOnInit() {
  }

  onFederacaoSubmit(){

    const federacao = {
      name: this.name,
      cnpj: this.cnpj,
      razaosocial: this.razaosocial,
      status: this.status,
      email: this.email
    }


     // Required Fields
    //  if(!this.validateService.validateFederacao(federacao)){
    //    this.flashMessage.show('Para continuar  necessrio preencher todos os campos', {cssClass:'alert-danger', timeout:3000});
    //    return false;
    //  }
   //
  //   // Validar o email
  //   if(!this.validateService.validateEmail(user.email)){
  //     this.flashMessage.show('Para continuar  necessrio informar um e-mail vlido', {cssClass:'alert-danger', timeout:3000});
  //     return false;
  //   }
   //

    // Register user


    this.federacaoService.addFederacao(federacao).subscribe(data => {
      if(data.success){
        //console.log(data);
        this.router.navigate(['federacao-view'], {queryParams: {id: data.id}});
        this.flashMessage.show('Federao registrada com sucesso.', {cssClass:'alert-success', timeout:3000});
      } else {
        this.flashMessage.show('Ocorreu um erro ao tentar registrar seu usurio. Favor entre em contato com o suporte tcnico do sistema.', {cssClass:'alert-danger', timeout:3000});
      }
    })
    }
  };



